export default function mapCountries(arr) {
  const result = [];
  for (let index in arr) {
    let name = arr[index].iso_3166_1
      ? ISO_3166_1[arr[index].iso_3166_1]
      : ISO_3166_1[arr[index].origin_country];
    if (!result.includes(name)) {
      result.push(name);
    }
  }
  return result;
}
