const GENRES = {
  28: "",
  12: "",
  16: "",
  35: "",
  80: "",
  99: "",
  18: "",
  10751: "",
  14: "",
  36: "",
  27: "",
  10402: "",
  9648: "",
  10749: "",
  878: "SF",
  10770: "TV ",
  53: "",
  10752: "",
  37: "",
};

export default function mapGenres(genres) {
  const result = [];
  for (let index in genres) {
    let name = GENRES[genres[index].id];
    if (!result.includes(name)) {
      result.push(name);
    }
  }
  return result;
}
